import { HttpClient } from '@angular/common/http';
import { BackendAPIService } from 'src/environments/backendAPIs';
import * as core from '@angular/core';
import { Observable } from 'rxjs';
import { PaginatedListResponse } from 'src/app/common/responseTypes/paginated-list-response.model';
import { DataResponse } from 'src/app/common/responseTypes/data-response.model';
@core.Injectable()
export class UserService {
    constructor(private http: HttpClient,
                private backendAPIs: BackendAPIService) {}

    getUsers(pageSize: number, pageNumber: number): Observable<PaginatedListResponse> {
       return this.http.get<PaginatedListResponse>(this.backendAPIs.getUsersListAPI(pageSize, pageNumber));
    }

    getLoggedInUser(): Observable<DataResponse> {
        return this.http.get<DataResponse>(this.backendAPIs.getLoggedInUserAPI());
    }

}
