import { Component, OnInit, OnDestroy, Input, ViewChild, ComponentFactoryResolver } from '@angular/core';
import { OnboardingStep } from '../onboarding-components/onboarding.step';
import { OnboardingDirective } from '../onboarding-components/onboardinghost.directive';
import { OnboardingComponent } from '../onboarding-components/onboarding.component';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RetailOffer } from './retailoffer.model';
import { RetailExploringOnboardingComponent } from '../onboarding-components/retail-exploring-onboarding/retail-exploring-onboarding.component';
import { OnboardingFormNotfoundErrorComponent } from '../onboarding-components/onboarding-form-notfound-error/onboarding-form-notfound-error.component';

@Component({
  selector: 'app-retail-onboarding',
  templateUrl: './retail-onboarding.component.html',
  styleUrls: ['./retail-onboarding.component.css']
})
export class RetailOnboardingComponent implements OnInit, OnDestroy  {

  offers: RetailOffer[] = [];
  selectedOffer: RetailOffer;
  breakpoint: number;
  onboardingSteps: OnboardingStep[];
  currentAdIndex = -1;
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  @ViewChild(OnboardingDirective, {static: true}) onboardingHost: OnboardingDirective;
  interval: any;
  constructor(private componentFactoryResolver: ComponentFactoryResolver, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.offers.push(this.getExploringOffer());
    this.offers.push(this.getEnthusiasticOffer());
    this.offers.push(this.getProOffer());

    this.breakpoint = (window.innerWidth <= 900) ? 1 : this.offers.length;

    this.onboardingSteps =
    [
      new OnboardingStep(RetailExploringOnboardingComponent, {name: 'Bombasto', bio: 'Brave as they come'})
    ];
  }

  ngOnDestroy(): void {
    clearInterval(this.interval);
  }

  loadOfferForm() {
    const componentFactory =
      this.componentFactoryResolver
      .resolveComponentFactory(this.resolveFormComponent(this.selectedOffer.formName));

    const viewContainerRef = this.onboardingHost.viewContainerRef;
    viewContainerRef.clear();

    const componentRef = viewContainerRef.createComponent<OnboardingComponent>(componentFactory);
    componentRef.instance.data = this.selectedOffer;
  }
  resolveFormComponent(formName) {
    const formComponents = [
      { id: 'RetailExploringOnboardingComponent', component: RetailExploringOnboardingComponent}
    ];
    const selectedFormComponent = formComponents.find((x => x.id === formName));
    if (selectedFormComponent == null){
      return OnboardingFormNotfoundErrorComponent;
    }
    return selectedFormComponent.component || OnboardingFormNotfoundErrorComponent;
  }
  onResize(event) {
    this.breakpoint = (event.target.innerWidth <= 900) ? 1 : this.offers.length;
  }
  onOfferSelect(selectedOffer: RetailOffer) {
    this.offers.forEach( (offer) => {
      offer.recomended = (offer.id === selectedOffer.id);
    });
    this.selectedOffer = selectedOffer;
    this.loadOfferForm();
  }
  getExploringOffer(): RetailOffer {
    const exploring: RetailOffer = new RetailOffer();
    exploring.id = 1;
    exploring.title = 'Exploring';
    exploring.subtitle = 'No Strings Attached';
    exploring.description = 'Explore our offering without opening a savings account. Best if you would like to view our interface and decide later! We just need your email or phone number to begin with.';
    exploring.formName = 'RetailExploringOnboardingComponent';
    return exploring;
  }
  getEnthusiasticOffer(): RetailOffer {
    const enthusiastic: RetailOffer = new RetailOffer();
    enthusiastic.id = 2;
    enthusiastic.recomended = true;
    enthusiastic.title = 'Enthusiastic';
    enthusiastic.subtitle = 'Get your savings account in 2 mins!';
    enthusiastic.description = 'Open your accounts with us. Provide us your Aadhar card, Pan card and initial seed money of Rs. 10,000';
    enthusiastic.formName = 'RetailEnthusiasticOnboardingComponent';
    return enthusiastic;
  }

  getProOffer(): RetailOffer {
    const pro: RetailOffer = new RetailOffer();
    pro.id = 3;
    pro.title = 'Pro';
    pro.subtitle = 'Save like a Professional';
    pro.description = 'Get the best of our technological and analytical skills to save your money like a professional';
    pro.formName = 'RetailProOnboardingComponent';
    return pro;
  }
}
