import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WelcomeComponent } from './modules/welcome/welcome-component/welcome-component.component';
import { DashboardComponent } from './modules/dashboard/dashboard/dashboard.component';
import { LogoutComponent } from './modules/login/logout/logout.component';
import { RetailOnboardingComponent } from './modules/onboarding/retail-onboarding/retail-onboarding.component';
import { EmailVerificationComponent } from './modules/users/email-verification/email-verification.component';
import { UserStateGuard } from './common/guards/LoggedInGuard';
import { PrivacyPolicyComponent } from './modules/documentation/privacy-policy/privacy-policy.component';

const routes: Routes = [
  {path: '', redirectTo: 'welcome', pathMatch: 'full'},
  {path: 'login', redirectTo: 'welcome'},
  {path: 'welcome', component: WelcomeComponent},
  {path: 'retail-onboarding', component: RetailOnboardingComponent},
  {path: 'email-verify', component: EmailVerificationComponent},
  {path: 'dashboard', component: DashboardComponent, canActivate: [UserStateGuard]},
  {path: 'logout', component: LogoutComponent},
  {path: 'privacy', component: PrivacyPolicyComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
