import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { LoginService } from 'src/app/modules/login/login.service';
import { User } from 'src/app/modules/users/user.model';
import { UserService } from 'src/app/modules/users/user.service';
@Injectable()
export class UserStateGuard implements CanActivate {
    constructor(private loginService: LoginService,
                private userService: UserService
    ) { }

    canActivate(): Observable<boolean> {
        const isAuthenticated = this.loginService.isAuthenticated();
        console.log(sessionStorage.getItem('authenticated') === 'true');
        if (sessionStorage.getItem('authenticated') === 'true') {
            const loggedInUser = this.userService.getLoggedInUser();
            loggedInUser.subscribe(data => {
                console.log(data);
                if (!data) {
                    // No response from API
                    console.log('No response from API');
                    return of(false);
                }
                if (data.status.status === 'SUCCESS') {
                    const user: User = data.data;
                    if (!user.accountNonExpired) {
                        // account expired
                        console.log('Account Expired');
                        return of(false);
                    }
                    if (!user.accountNonLocked) {
                        // account locked
                        console.log('Account Locked');
                        return of(false);
                    }
                    if (!user.credentialsNonExpired) {
                        // credentials expired
                        console.log('credentials expired');
                        return of(false);
                    }
                    if (!user.termsAccepted) {
                        // Terms not Accepted
                        console.log('Terms not Accepted');
                        return of(false);
                    }
                    if (user.firstTimePwdReset) {
                        // First Time Pwd Reset
                        console.log('First Time Pwd Reset');
                        return of(false);
                    }
                    console.log(' All well, move ahed');
                    return of(true);
                }
                else {
                    console.log(' some error');
                    return of(false);
                }
            }, error => {
                console.log(' some error');
                return of(false);
            });
            return of(true);
        }
        else {
            console.log(' user is not authenticated');
            return of(false);
        }
    }
}
