import {Injectable} from '@angular/core';
import {HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse} from '@angular/common/http';
import { Observable} from 'rxjs';
import { MockResponseService } from './mock/mockresponse.service';


@Injectable()
export class MockInterceptor implements HttpInterceptor {
  constructor(private mockResponseService: MockResponseService ) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const mock = sessionStorage.getItem('mock');
    if (mock === 'true' && !request.url.startsWith('assets')) {
        const mockRequestUrl = this.mockResponseService.getMockRequestUrl(request);
        const mockRequest = request.clone({url: mockRequestUrl, method: 'GET'});
        return next.handle(mockRequest);
    } else {
      // mocks are disabled here
      return next.handle(request);
     }
  }
}
